.carusel-slide-inner {
	padding: 5px;
	margin: 5px;
	margin-bottom: 50px;
	background: var(--wp--preset--color--gray-ultra-light);
	border-radius: 12px;
	text-align: center;
}
.carusel-img img {
	width: 100%;
	height: auto;
	border-radius: 8px;
}
.carusel-img img:hover {
	cursor: pointer;
}
.carusel-title {
	margin-top: 1rem;
	font-size: 1.25rem;
}
.carusel-excerpt {
	font-size: 0.9rem;
	color: var(--wp--preset--color--gray-medium);
}

.carusel-swiper-container {
	padding: 0 5%; /* margine ai lati */
	position: relative;

	/* Personalizzazione dei bullet della paginazione */
	.swiper-pagination-bullet {
		width: 22px !important; /* Dimensione del dot */
		height: 22px !important; /* Dimensione del dot */
		background-color: var(
			--wp--preset--color--light-blue
		) !important; /* Colore del dot */
		border-radius: 50% !important; /* Rende il dot rotondo */
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.8) !important; /* Ombra sui dots */
	}

	/* Dot attivo - Cambia il colore e l'ombra */
	.swiper-pagination-bullet-active {
		background-color: var(
			--wp--preset--color--light-blue
		) !important; /* Colore del dot attivo */
		box-shadow: 0 0 10px rgba(0, 0, 0, 1) !important; /* Ombra più intensa per il dot attivo */
	}

	/* Dot non attivo - Modifica la trasparenza e aggiungi il bordo */
	.swiper-pagination-bullet:not(.swiper-pagination-bullet-active) {
		opacity: 1 !important; /* Opacità dei dot non attivi */
		border: 2px solid var(--wp--preset--color--light-blue) !important; /* Bordi bianchi */
		border-radius: 50% !important; /* Rende il dot rotondo */
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.8) !important; /* Ombra sui dots non attivi */
		background-color: transparent !important; /* Colore del dot */
	}

	/* Hover sui bullet - Aumenta la dimensione e cambia il colore al passaggio del mouse */
	.swiper-pagination-bullet:hover {
		background-color: var(
			--wp--preset--color--light-blue
		) !important; /* Colore al passaggio del mouse */
		transition: background-color 0.3s ease !important; /* Transizione al passaggio del mouse */
		width: 24px !important; /* Aumenta la dimensione al passaggio del mouse */
		height: 24px !important; /* Aumenta la dimensione al passaggio del mouse */
	}
}

.carusel-swiper-container .swiper-slide {
	width: 70%; /* slide principale più grande */
	transition: transform 0.3s ease;
	opacity: 0.5; /* laterali più trasparenti */
}

.carusel-swiper-container .swiper-slide-active {
	width: 90%;
	opacity: 1; /* slide centrale opaca */
	transform: scale(1.05); /* leggero ingrandimento */
}

.carusel-swiper-container .swiper-pagination {
	margin-top: 20px;
	text-align: center;
}

.swiper-button-prev,
.swiper-button-next {
	display: none; /* nascondi frecce se vuoi un look pulito */
}
